/* basic styles */
.skeleton {
    background-color: #ddd;
    margin: 10px 0;
    border-radius: 4px;
}

.skeleton.text {
    width: 100%;
    height: 12px;
}

.skeleton.title {
    width: 50%;
    height: 20px;
    margin-bottom: 15px;
}

.skeleton.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.skeleton.thumbnail {
    width: 100px;
    height: 100px;
}

.skeleton-wrapper {
    position: relative;
    margin: 20px auto;
    padding: 10px 15px;
    border-radius: 4px;
}

/* skeleton profile */
.skeleton-profile {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 30px;
    align-items: center;
}

/* themes */
.skeleton-wrapper.light {
    background-color: #f2f2f2;
}

.skeleton-wrapper.dark {
    background-color: #444;
}

.skeleton-wrapper.dark .skeleton {
    background-color: #777;
}

/* animation effect */
.shimmer-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation: loading 2.5s infinite;
    overflow: hidden;
}

.shimmer {
    width: 50%;
    height: 100%;
    background-color: rgba(255,255,255,0.2);
    transform: skewX(-20deg);
    box-shadow: 0 0 30px 30px rgba(255,255,255,0.05);
}
.dark .shimmer {
    background-color: rgba(255,255,255,0.05);
}

@keyframes loading {
    0% {transform: translateX(-150%)}
    50% {transform: translateX(-60%)}
    100% {transform: translateX(150%)}
}
